<template>
    <div class="pa-wrapper">
        <div class="head-block">
            <div class="head-image">
                <div class="head-image__img">
                    <img :src="Avatar" alt="avatar">
                </div>
                <div class="head-image__status">
                    <img :src="StatusImg" alt="status">
                </div>
            </div>
            <div class="head-block__name">{{ userName }}</div>
        </div>
        <div class="karma-block">
            <div class="karma-block__points">
                Карма: {{ userPoints }} балів
            </div>
            <div class="karma-block__level">
                {{ userLevel }} рівень/ <span>{{ forNextLevel }} балів до наступного рівня</span>
            </div>
            <div class="karma-progress-box">
                <div class="karma-progress-box__line"></div>
            </div>
        </div>
        <div class="rating">
            <Rating
                v-for="item in rating"
                :key="item.id"
                :percentages="item.rating"
                :imgSrc="item.imgSrc"
                :imgAlt="item.imgAlt"
                :text="item.text"
            />
        </div>
        <div class="be-volunteer">
            Поганий той глядач, що не хоче <a href="#">стати волонтером <img :src="HelpBeVolunteerIcon" alt="help"></a>
        </div>
        <div class="invite-friends">
            <div class="invite-friends__wrapper">
                <h2 class="invite-friends__title">Запроси друга</h2>
                <h4 class="invite-friends__subtitle">Не будь жадібним! Поділися посиланням з друзями.</h4>
                <Input
                    v-for="field in inviteFriends"
                    :key="field.id"
                    :id="field.id"
                    :label="field.label"
                    :type="field.type"
                    :showError="field.showError"
                    :errorMessage="field.errorMessage"
                    @handle-input="handleInput($event, field.id)"
                    @on-click="isClicked(field.id)"
                />
                <button
                    class="invite-friends-btn"
                    @click="addNewEmail"
                >
                    <img
                        :src="InviteFriendsIcon"
                        alt="invite icon"
                        class="invite-friends-btn__icon"
                    >
                    <div class="invite-friends-btn__text">Додати ще один email <span>(+10 до карми)</span></div>
                </button>
                <Button
                    className="send-invite"
                    text="Надіслати запрошення"
                    type="blue"
                    :disabled="!allIsValid"
                />
            </div>
        </div>
    </div>
</template>

<script>
import is from 'is_js'

import Rating from '@/components/HomePage/Rating'
import Input from '@/components/Input'
import Button from '@/components/Buttons/Button'

import Avatar from '@/assets/img/actor.png'
import StatusImg from '@/assets/img/pa-status.svg'
import HandLike from '@/assets/img/hand-cool.svg'
import HandFingerUp from '@/assets/img/hand-finger-up.svg'
import HandWrite from '@/assets/img/hand-write.svg'
import HelpBeVolunteerIcon from '@/assets/img/icon-help-be-volunteer.svg'
import InviteFriendsIcon from '@/assets/img/icon-plus-with-circle.svg'

function newEmailData(value = '') {
    const randomId = Math.round(Math.random() * 100000)
    const inputData = {
        id: randomId,
        label: 'Email',
        type: 'email',
        showError: false,
        errorMessage: 'Це поле обов\'язкове',
        value: value,
        touched: false,
        valid: false
    }
    return inputData
}

function checkValidation(array, id) {
    array.forEach(item => {
        if (item.id === id) {
            is.email(item.value)
            ? item.valid = true
            : item.valid = false

            item.touched && !item.valid
            ? item.errorMessage = item.value === '' ? 'Це поле обов\'язкове' : 'Введіть коректний email'
            : null
        }
    })
}

function isAllIsValid(array) {
    let valid = true
    array.forEach(item => {
        if (item.touched && item.valid && valid) {
            valid = true
        } else {
            valid = false
        }
    })
    return valid
}

export default {
    components: {
        Rating, Input, Button
    },
    data () {
        return {
            Avatar,
            StatusImg,
            HelpBeVolunteerIcon,
            InviteFriendsIcon,
            userName: 'Толік Анатолік',
            userPoints: 1480,
            userLevel: 3,
            forNextLevel: 20,
            rating: [
                {
                    id: 1,
                    rating: 87,
                    imgSrc: HandLike,
                    imgAlt: 'like',
                    text: 'Вподобайко'
                },
                {
                    id: 2,
                    rating: 50,
                    imgSrc: HandFingerUp,
                    imgAlt: 'finger up',
                    text: 'Критикан'
                },
                {
                    id: 3,
                    rating: 61,
                    imgSrc: HandWrite,
                    imgAlt: 'write',
                    text: 'Писака'
                },
            ],
            inviteFriends: [
                newEmailData()
            ],
            allIsValid: false
        }
    },
    methods: {
        handleInput: function(message, id) {
            this.inviteFriends.forEach(item => item.id === id ? item.value = message : null)
            checkValidation(this.inviteFriends, id)
            this.allIsValid = isAllIsValid(this.inviteFriends)
        },
        addNewEmail: function() {
            if (this.inviteFriends.length >= 9) {
                return
            }
            this.inviteFriends.push(newEmailData())
            this.allIsValid = isAllIsValid(this.inviteFriends)
        },
        isClicked: function(id) {
            this.inviteFriends.forEach(item => item.id === id ? item.touched = true : null)
        }
    }
}
</script>

